import React, {useState} from 'react';
import axios from 'axios';

const GoogleLogin = () => {
    const [user, setUser] = useState('')


    const handleGoogleLogin = async () => {
        try {
            const response = await axios.get('http://127.0.0.1:3003/users/auth/google_oauth2/callback');
            console.log('Respuesta de inicio de sesin con Google:', response.data);
            setUser(response.data)
        } catch (error) {
            console.error('Error al iniciar sesin con Google:', error);
        }

    };
    console.log(user)
    return (
        <div>
            <h2>Iniciar sesin con Google</h2>
            <button onClick={handleGoogleLogin}>Iniciar sesin con Google</button>
        </div>
    );
};

export default GoogleLogin;
